<template>
  <div style="height:600px;">
    <el-table
      :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
      style="width: 100%;height: 480px"
      stripe
    >
      <el-table-column label="Id" prop="id"></el-table-column>
      <el-table-column label="Name" prop="name"></el-table-column>
      <el-table-column label="Gender" prop="gender"></el-table-column>
      <el-table-column label="Birth" prop="birth" :formatter="formatDate"></el-table-column>
      <el-table-column label="Edu" prop="edu"></el-table-column>
      <el-table-column label="Contact" prop="contact"></el-table-column>
      <el-table-column label="Politicestatus" prop="politicestatus"></el-table-column>
      <el-table-column label="Major" prop="major"></el-table-column>
      <el-table-column align="right">
        <template slot="header">
          <el-input v-model="search" size="mini" placeholder="输入关键字搜索" />
        </template>
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">Edit</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">Delete</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-row style="margin:30px 0px;">
      <el-col :span="10" :offset="14">
        <div class="block">
          <el-pagination
            layout="prev, pager, next,jumper,sizes"
            @current-change="findPage"
            @size-change="changeSize"
            :page-size="pagesize"
            :current-page="currentpage"
            prev-text="上一页"
            next-text="下一页"
            small
            background
            :page-sizes="[2,4,6]"
            :total="total"
          ></el-pagination>
        </div>
      </el-col>
    </el-row>

    <el-button style="margin:0px 0px;" type="success" size="medium" @click="showChange">添加</el-button>

    <div :class="theclass">
      <transition name="el-fade-in-linear">
        <div v-if="show" class="transition-box">
          <el-form ref="form" :model="form" label-width="80px" style="width: 300px;" resetFields>
            <el-form-item>添加学生信息</el-form-item>

            <el-form-item label="学生id">
              <el-input v-model="form.id"></el-input>
            </el-form-item>
            <el-form-item label="学生姓名">
              <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="密码">
              <el-input show-password v-model="form.password"></el-input>
            </el-form-item>
            <el-form-item label="性别">
              <el-radio-group v-model="form.gender">
                <el-radio label="男"></el-radio>
                <el-radio label="女"></el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item label="出生日期" style="width: 400px;">
              <el-col :span="11">
                <el-date-picker
                  v-model="form.birth"
                  type="date"
                  placeholder="选择日期"
                  format="yyyy-MM-dd"
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </el-col>
            </el-form-item>
            <el-form-item label="民族">
              <el-input v-model="form.nation"></el-input>
            </el-form-item>

            <el-form-item label="学历">
              <el-select v-model="form.edu" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="联系方式">
              <el-input v-model="form.contact"></el-input>
            </el-form-item>

            <el-form-item label="专业">
              <el-input v-model="form.major"></el-input>
            </el-form-item>

            <el-form-item label="email">
              <el-input v-model="form.email"></el-input>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="onSubmit">立即创建</el-button>
              <el-button @click="resetForm('form')">取消</el-button>
            </el-form-item>
          </el-form>
        </div>
      </transition>
    </div>

    <div :class="Secondclass">
      <el-collapse-transition>
        <div v-if="show1">
          <div class="transition-box1">
            <el-form
              ref="updates"
              :model="form"
              label-width="80px"
              style="width: 300px;"
              resetFields
            >
              <el-form-item>修改学生信息</el-form-item>

              <el-form-item label="学生id">
                <el-input v-model="updates.id"></el-input>
              </el-form-item>
              <el-form-item label="学生姓名">
                <el-input v-model="updates.name"></el-input>
              </el-form-item>
              <el-form-item label="密码">
                <el-input show-password v-model="updates.password"></el-input>
              </el-form-item>
              <el-form-item label="性别">
                <el-radio-group v-model="updates.gender">
                  <el-radio label="男"></el-radio>
                  <el-radio label="女"></el-radio>
                </el-radio-group>
              </el-form-item>

              <el-form-item label="出生日期" style="width: 400px;">
                <el-col :span="11">
                  <el-date-picker
                    v-model="updates.birth"
                    type="date"
                    placeholder="选择日期"
                    format="yyyy-MM-dd"
                    value-format="yyyy-MM-dd"
                  ></el-date-picker>
                </el-col>
              </el-form-item>
              <el-form-item label="民族">
                <el-input v-model="updates.nation"></el-input>
              </el-form-item>

              <el-form-item label="学历">
                <el-select v-model="updates.edu" placeholder="请选择">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="联系方式">
                <el-input v-model="updates.contact"></el-input>
              </el-form-item>

              <el-form-item label="专业">
                <el-input v-model="updates.major"></el-input>
              </el-form-item>

              <el-form-item label="email">
                <el-input v-model="updates.email"></el-input>
              </el-form-item>

              <el-form-item>
                <el-button type="primary" @click="onUpdate">确定修改</el-button>
                <el-button @click="resetForm('updates')">取消</el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </el-collapse-transition>
    </div>
  </div>
</template>

<script>
export default {
  name: "Userlist",
  data() {
    return {
      currentpage: 1,
      pagesize: 4,
      total: 0,
      theclass: "static1",
      Secondclass: "updatestatic",
      tableData: [],
      search: "",
      show: false,
      show1: false,
      form: {
        id: "",
        name: "",
        password: "",
        gender: "男",
        birth: "",
        edu: "",
        nation: "",
        contact: "",
        major: "",
        email: ""
      },
      updates: {
        id: null,
        name: "",
        password: "",
        gender: "男",
        birth: "",
        edu: "",
        nation: "",
        contact: "",
        major: "",
        email: ""
      },
      returnMessage: "",
      options: [
        {
          value: "本科",
          label: "本科"
        },
        {
          value: "大专",
          label: "大专"
        },
        {
          value: "硕士",
          label: "硕士"
        },
        {
          value: "博士",
          label: "博士"
        },
        {
          value: "研究生",
          label: "研究生"
        }
      ]
    };
  },
  methods: {
    formatDate(row, column) {//格式化日期
      // 获取单元格数据
      let data = row[column.property];
      if (data == null) {
        return null;
      }
      let dt = new Date(data);
      return (
        dt.getFullYear() +
        "-" +
        (dt.getMonth() + 1) +
        "-" +
        dt.getDate() 
      );
    },

    findPage(page) {
      //用来处理分页相关方法
      //console.log(page);
      this.currentpage = page;
      this.findAllStudent(page, this.pagesize);
    },
    changeSize(size) {
      //用来处理每页显示记录变化的方法
      // console.log(size)
      this.pagesize = size;
      this.findAllStudent(this.currentpage, size);
    },
    handleEdit(index, row) {
      if (this.show == true) {
        this.resetForm(this.form);
      }
      console.log(index, row);
      this.show1 = true;
      this.Secondclass = "updateactive";
      var that = this;
      var url = "http://localhost:8989/stu/selectOneStu/" + row.id;
      this.$http.get(url).then(res => {
        console.log(res.data.msg);
        that.updates = res.data.data;
      });
    },
    handleDelete(index, row) {
      console.log(index, row);
      this.$confirm("此操作将永久删除该学生信息, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteStudentById(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    showChange() {
      if (this.show1 == true) this.resetForm(this.updates);
      this.show = true;
      this.theclass = "active1";
    },
    resetForm(formName) {
      if (this.show == true) {
        this.show = false;
        this.theclass = "static1";
      }
      if (this.show1 == true) {
        this.show1 = false;
        this.Secondclass = "updatestatic";
      }
      this.$refs[formName].resetFields();
      this.form = {};
      this.form.gender = "男";
      this.updates = {};
      this.updates.gender = "男";
      //.log(this.form)
    },
    onUpdate() {
      this.$confirm("确定修改?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.updateStudent();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消修改"
          });
        });
    },
    onSubmit() {
      var that = this;
      this.$http
        .post("http://localhost:8989/teacher/addStudent", this.form)
        .then(res => {
          console.log(that.form);
          console.log(res.data.msg);
          that.returnMessage = res.data.msg;
          this.$message(this.returnMessage);
          that.resetForm("form");
          that.findAllStudent();
        })
        .catch(err => {
          console.log(that.form);
          that.resetForm("form");
          this.$message("添加学生失败！");
        });

      // console.log('submit!');
    },
    findAllStudent(currentpage, pagesize) {
      // var that = this;
      // this.$http
      //   .get("http://localhost:8989/teacher/getAllStudent")
      //   .then(res => {
      //     console.log(res.data.data);
      //     that.tableData = res.data.data;
      //   });
      var that = this;
      currentpage = currentpage ? currentpage : that.currentpage;
      pagesize = pagesize ? pagesize : that.pagesize;
      var url =
        "http://localhost:8989/teacher/queryUser/" +
        currentpage +
        "/" +
        pagesize;
      this.$http
        .get(url)
        // /queryUser/{current}/{size}
        .then(res => {
          //console.log(res.data);
          that.tableData = res.data.studentsList;
          that.total = res.data.total;
        });
    },
    deleteStudentById(id) {
      var that = this;
      var url = "http://localhost:8989/teacher/deleteStudent/" + id;
      this.$http
        .get(url)
        .then(res => {
          // console.log(res.data.data);
          this.findAllStudent();
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(err => {
          this.$message.error("删除学生失败");
        });
    },
    updateStudent() {
      var that = this;
      this.$http
        .post("http://localhost:8989/stu/update", this.updates)
        .then(res => {
          console.log(res.data.msg);
          that.returnMessage = res.data.msg;
          this.$message(this.returnMessage);
          that.resetForm("updates");
          that.findAllStudent();
        })
        .catch(err => {
          that.resetForm("updates");
          this.$message("修改学生失败！");
        });
    }
  },
  created() {
    this.findAllStudent();
  }
};
</script>

<style>
.transition-box {
  margin-bottom: 0px;
  width: 90%;
  height: 100px;
  border-radius: 6px;
  text-align: center;
  color: black;
  padding: 40px 20px;
  box-sizing: border-box;
  margin-right: 20px;
}
.static1 {
  display: flex;
  width: 400px;
  height: 500px;
  z-index: 100;
  position: fixed;
  bottom: 5%;
  left: 10%;
}
.active1 {
  display: flex;
  width: 400px;
  height: 100%;
  z-index: 100;
  position: fixed;
  bottom: 5%;
  left: 10%;
  background-color: white;
}
.updatestatic {
  display: flex;
  width: 400px;
  height: 500px;
  z-index: 100;
  position: fixed;
  bottom: 5%;
  left: 30%;
}
.updateactive {
  display: flex;
  width: 400px;
  height: 100%;
  z-index: 100;
  position: fixed;
  bottom: 5%;
  left: 30%;
  background-color: lightcyan;
}
.transition-box1 {
  margin-bottom: 0px;
  width: 90%;
  height: 100px;
  border-radius: 6px;
  text-align: center;
  color: black;
  padding: 40px 20px;
  box-sizing: border-box;
  margin-right: 20px;
}
</style>